<template>
    <div class="license-track-info"
        v-if="!data.loading"
        v-show="!data.updating || props.viewMode === 'table'"
        :class="{
            'view-mode-table': props.viewMode === 'table',
            'updating': data.updating
        }"
        ref="licenseTrackInfoBox"
    >
        <label v-if="props.viewMode === 'list'"
            class="title">{{ t(`pages.licenses.tracks.edit.title`) }}</label>

        <Sections :item="data.item"
            :viewMode="props.viewMode"
            :permissions="{
                canEditLicense
            }"
            @change="onSectionChange"
            @error="onSectionError"
            @save="saveChanges"
            @sections="emit('sections', $event)"
        />

        <div :class="props.viewMode === 'table' ? 'actions-box detail column' : ''">
            <Teleport :to="`#${props.toolbarId}`" :disabled="props.viewMode !== 'list'">
                <span class="devider"  v-if="props.viewMode === 'list'" />

                <KebabMenu
                    v-if="props.track?.id && props.viewMode === 'list'"
                    :leftAuto="!locale.rtl"
                    :rightAuto="locale.rtl"
                    :tertiary="true"
                    :invert="true"
                    size="size-s"
                >
                    <ul>
                        <li @click="close">
                            <Icon icon="close" />
                            <span>{{ t(`pages.licenses.tracks.cancel`) }}</span>
                        </li>
                    </ul>
                </KebabMenu>

                <Button :name="t(`pages.licenses.tracks.edit.save`)"
                    class="size-s"
                    :disabled="saveDisabled"
                    v-if="tracks.canEditLicense({
                        track: data.item,
                        artist: props.artist
                    })"
                    :loading="data.uploading"
                    :errorsTooltip="errorsTooltip"
                    :class="{
                        tertiary: data.uploading,
                        active: data.uploading,
                        invert: data.uploading,
                        secondary: !data.uploading
                    }"
                    @click="saveChanges"
                />
            </Teleport>
        </div>
    </div>
    <div class="loader" v-if="data.loading || (data.updating && props.viewMode !== 'table')">
        <div>
            <Loader :inside="true" class="tertiary" />
        </div>
    </div>
</template>

<script setup>
    import { reactive, computed, onMounted, ref, watch } from 'vue';

    import Sections from './sections/Sections.vue';

    import { useI18n } from 'vue-i18n';
    import api from 'ðŸ”—/api';
    import Button from 'ðŸ”—/components/ui/Button.vue';
    import KebabMenu from 'ðŸ”—/components/ui/KebabMenu.vue';
    import Loader from 'ðŸ”—/components/ui/Loader.vue';
    import { delay, message } from 'ðŸ”—/utils/utils';
    import stores from '@/stores';
    import { code } from 'ðŸ”—/utils/utils';

    const modals = stores.modals();

    const tracks = api.tracks();
    const licenses = api.licenses();
    const locale = stores.locale();

    const emit = defineEmits(['update', 'add', 'remove', 'edit', 'sections', 'close', 'hide', 'actions']);
    const { t } = useI18n();

    const licenseTrackInfoBox = ref(null);

    const props = defineProps({
        toolbarId: { type: String, default: '' },
        track: { type: [Object, null], default: null },
        artist: { type: [Object, null], default: null },
        viewMode: { type: String, default: 'list' }
    });

    // Permissions
        const canEditLicense = computed(() => {
            return tracks.canEditLicense({
                track: props.track
            });
        });

    const data = reactive({
        item: {},
        updatedItem: {},
        errors: {},
        loading: true,
        uploading: false
    });

    const onSectionError = (payload) => {
        if (!payload || !Object.keys(payload)?.length) {
            return;
        }

        Object.keys(payload).forEach(field => {
            if (payload[field]?.length) {
                data.errors[field] = payload[field];
            } else {
                delete data.errors[field];
            }
        });
    }

    const onSectionChange = (payload) => {
        if (!payload || !Object.keys(payload)?.length) {
            return;
        }

        Object.keys(payload).forEach(field => data.updatedItem[field] = payload[field]);

        emit('edit', {
            ...data.item,
            ...data.updatedItem
        });
    }

    const hasChanges = computed(() => {
        if (!data.updatedItem) {
            return false;
        }

        return !!Object.keys(data.updatedItem)?.length;
    });

    const saveDisabled = computed(() => {
        return !data.updatedItem.licenses || !!Object.keys(data.errors)?.length || data.uploading || !hasChanges.value;
    });

    watch(
        () => saveDisabled.value,
        (value) => {
            if (value) {
                emit('actions', {
                    item: data.item,
                    actions: []
                });
            } else {
                emit('actions', {
                    item: data.item,
                    actions: [
                        {
                            type: 'save',
                            action: saveChanges
                        }
                    ]
                });
            }
        }
    );

    const errorsTooltip = computed(() => {
        const errors = [];
        Object.values(data.errors)?.forEach(item => errors.splice(0, 0, ...item));
        return errors.join('<br>');
    });


    const close = () => {
        emit('close', data.item);
    }

    const saveChanges = async () => {
        let scrollTop = licenseTrackInfoBox.value?.closest('.item-detail')?.scrollTop;

        data.updating = true;

        // ÐžÐ±Ð½Ð¾Ð²Ð»ÑÐµÐ¼ Ð»Ð¸Ñ†ÐµÐ½Ð·Ð¸Ð¸
        if (data.updatedItem?.licenses) {
            const oldIds = data.item?.licenses?.filter(item => item.id).map(item => item.id) || [];
            const newIds = data.updatedItem?.licenses?.filter(item => item.id).map(item => item.id) || [];

            // Ð£Ð´Ð°Ð»ÑÐµÐ¼ Ð»Ð¸ÑˆÐ½Ð¸Ðµ
            await Promise.all(oldIds.filter(id => newIds.indexOf(id) === -1).map(async (id) => {
                await licenses.tracks.remove(id);
            }));

            // ÐžÐ±Ð½Ð¾Ð²Ð»ÑÐµÐ¼ Ð¸Ð·Ð¼ÐµÐ½ÐµÐ½Ð½Ñ‹Ðµ
            // await Promise.all(data.updatedItem?.licenses.filter(item => item.id && oldIds.indexOf(item.id) === -1).map(async (item) => {
            await Promise.all(data.updatedItem?.licenses.filter(item => !!item.id).map(async (item) => {
                await licenses.tracks.update(item.id, {
                    // provider: item.provider,
                    url: item.url
                });
            }));

            // Ð”Ð¾Ð±Ð°Ð²Ð»ÑÐµÐ¼ Ð½Ð¾Ð²Ñ‹Ðµ
            await Promise.all(data.updatedItem?.licenses.filter(item => !item.id).map(async (item) => {
                await licenses.tracks.add({
                    track: props.track.id,
                    provider: item.provider,
                    url: item.url
                });

            }));
        }

        await delay(200);
        data.updating = false;

        message.info(t(`pages.licenses.tracks.done.title`));

        data.item.licenses = (await licenses.tracks.list(props.track.id))?.items || [];
        data.updatedItem = {};

        emit('update', data.item);

        if (props.viewMode === 'list' && scrollTop) {
            setTimeout(() => {
                if (licenseTrackInfoBox?.value) {
                    licenseTrackInfoBox.value.closest('.item-detail').scrollTop = scrollTop;
                }
            });
        }
    }

    const getItem = async () => {
        data.loading = true;

        const payload = await licenses.tracks.list(props.track.id);

        data.item = {
            ...props.track,
            licenses: payload || []
        }

        await delay(200);

        data.loading = false;
    }

    onMounted(async() => {
        if (props.track?.id) {
            getItem();
        } else {
            close();
            data.loading = false;
        }
    });
</script>

<style lang="scss" scoped>
    @include studio.actions-box;

    div.loader {
        @include inside-loader;
        min-height: 132px;
    }

    .errors {
        width: 18px;
        height: 18px;
        fill: var(--white-500);
        opacity: 0;
        pointer-events: none;
        &.showed {
            opacity: 1;
            pointer-events: auto;
        }
    }

    .license-track-info {
        display: flex;
        flex-direction: column;
        gap: 24px;
        .title {
            font: var(--font-h3);
            font-weight: bold;
            color: var(--dark-500);
        }

        &.view-mode-table {
            flex-direction: row;
            gap: 0;
            min-width: calc(100% - 324px);

            .actions-box {
                width: 140px;
                box-shadow: -8px 0px 8px -12px var(--dark-500);
                z-index: 1;
                position: sticky;
                right: 0;
                flex-direction: column-reverse;
                justify-content: start;
                padding: 18px 8px;
                margin-left: auto;
            }

            &.updating {
                pointer-events: none;
                position: relative;
                &:after {
                    content: '';
                    opacity: .6;
                    @include skeleton;
                }

                .actions-box {
                    pointer-events: none;
                }
            }
        }
    }
    .direction-rtl .license-track-info {
        &.view-mode-table {
            .actions-box {
                right: auto;
                left: 0;
                box-shadow: 8px 0px 8px -12px var(--dark-500);
            }
        }
    }
</style>
