<template>
    <ItemsEditor
        :t="data.t"
        :code="data.code"
        :getItem="api.tracks().get"
        :items="props.items"
        @close="emit('close', $event)"
        ref="editor"
    >
        <template #page-header-title>
            <template v-if="props.artist || props.release">
                <PageHeaderArtistTitle :artist="props.artist" v-if="props.artist" />

                <Icon icon="chevron-right" class="devider" v-if="props.artist && props.release" />

                <PageHeaderReleaseTitle :release="props.release" v-if="props.release" />
            </template>

            <label v-else class="title">{{ t(`pages.licenses.tracks.title`) }}</label>
        </template>

        <template #items="{ id, items, change, sortable, sort }">
            <Tracks
                :id="id"
                :items="items"
                :showState="false"
                @change="change"
            />
        </template>

        <template #preview="{ item }">
            <TrackPreview :item="item" />
        </template>
        
        <template #item="{ toolbarId, position, item, viewMode, edit, add, update, remove, close, sections, hide, actions }">
            <LicenseTrackInfo
                :toolbarId="toolbarId"
                :track="item"
                :viewMode="viewMode"
                :artist="props.artist"
                @edit="edit"
                @update="update"
                @close="close"
                @sections="sections"
                @actions="actions"
            />
        </template>
    </ItemsEditor>
</template>

<script setup>
    import { useI18n } from 'vue-i18n';
    import { reactive, computed, onMounted, ref } from 'vue';

    import ItemsEditor from '@/components/ui/ItemsEditor/ItemsEditor.vue';
    import PageHeaderArtistTitle from '@/components/blocks/PageHeader/PageHeaderArtistTitle.vue';
    import PageHeaderReleaseTitle from '@/components/blocks/PageHeader/PageHeaderReleaseTitle.vue';

    import LicenseTrackInfo from '../LicenseTrackInfo/LicenseTrackInfo.vue';
    import Tracks from '../TrackEditor/Tracks.vue';
    import TrackPreview from 'ðŸ”—/components/ui/TrackPreview.vue';
    import api from 'ðŸ”—/api';

    const { t } = useI18n();
    const emit = defineEmits(['close']);

    const editor = ref(null);

    const props = defineProps({
        artist: { type: [Object, null], default: null },
        release: { type: [Object, null], default: null },
        items: { type: [Array, null], default: null }
    });

    const data = reactive({
        t: {
            title: 'pages.licenses.tracks.title',
            back: 'pages.licenses.tracks.back',
            'add-one': 'pages.licenses.tracks.add-one',
            'select-several': 'pages.licenses.tracks.select-several',
            empty: {
                title: 'pages.licenses.tracks.empty.title',
                description: 'pages.licenses.tracks.empty.description'
            },
            revert: {
                confirm: {
                    title: 'pages.licenses.tracks.revert.confirm.title',
                    description: 'pages.licenses.tracks.revert.confirm.description'
                }
            },
            unsaved: {
                confirm: {
                    title: 'pages.licenses.tracks.unsaved.confirm.title',
                    description: 'pages.licenses.tracks.unsaved.confirm.description'
                }
            }
        },
        code: 'license-track-editor',
    });

    onMounted(() => {
        if (props.items?.length) {
            data.item = props.items[0];
        }
    });
</script>

<style lang="scss" scoped>
    .title {
        font: var(--font-h6);
        font-weight: bold;
        color: var(--dark-500);
    }
</style>
