<template>
    <template v-if="data.ready">
        <FormLine v-for="(item, id) in data.licenses"
            :key="id"
            :viewMode="props.viewMode"
            :field="{
                width: fields.providers.width
            }"
        > 
            <template v-slot:title>
                <Select
                    class="noerror light title"
                    :optionList="data.providers"
                    :placeholder="t(`pages.licenses.tracks.fields.providers.title`)"
                    :leftAuto="false"
                    :disabled="!props.permissions.canEditLicense || item.id"
                    :maxValuesCount="1"
                    :rightAuto="false"
                    :clearable="false"
                    :value="item.provider"
                    @change="onProviderChange($event, item)"
                />
            </template>
            <template v-slot>
                <div class="box-row">

                    <Field
                        :value="item.url"
                        :placeholder="item.provider ? data.providers.find(provider => provider.id === item.provider)?.placeholder : t(`pages.licenses.tracks.fields.url.placeholder`)"
                        class="noerror light with-placeholder"
                        :disabled="!props.permissions.canEditLicense"
                        @change="onUrlChange($event, item)"
                    />

                    <IconButton
                        icon="minus"
                        class="size-m secondary"
                        :title="t(`pages.licenses.tracks.sections.providers.remove`)"
                        :disabled="!props.permissions.canEditLicense"
                        @click="remove(item)"
                    />
                </div>
            </template>
        </FormLine>

        <FormLine
            :viewMode="props.viewMode"
        >
            <template v-slot:title>
                <IconButton
                    icon="plus"
                    class="size-m "
                    :title="t(`pages.licenses.tracks.sections.providers.add`)"
                    :disabled="!props.permissions.canEditLicense"
                    @click="data.licenses.push({
                        provider: data.providers[0]?.id || null
                    })"
                />
            </template>
        </FormLine>
    </template>
</template>

<script setup>
    import { reactive, computed, onMounted } from 'vue';
    import { useI18n } from 'vue-i18n';
    
    import Select from 'ðŸ”—/components/ui/Select.vue';
    // import ArtistAutoComplete from 'ðŸ”—/components/ui/ArtistAutoComplete.vue';
    import IconButton from 'ðŸ”—/components/ui/IconButton.vue';
    import Button from 'ðŸ”—/components/ui/Button.vue';
    import Field from 'ðŸ”—/components/ui/Field.vue';
    import FormLine from '@/components/ui/FormLine.vue';
    import api from 'ðŸ”—/api';
    // import { data.providers } from 'ðŸ”—/constants';

    const emit = defineEmits(['change', 'error', 'metadata', 'save']);
    const { t } = useI18n();

    const props = defineProps({
        permissions: { type: Object, default: () => ({}) },
        item: { type: [Object, null], default: null },
        viewMode: { type: String, default: 'list' }
    });

    const fields = {
        providers: {
            width: 640
        }
    }

    emit('metadata', {
        fields,
        direction: 'column'
    });

    const data = reactive({
        providers: [],
        licenses: (props.item?.licenses || []).map(item => ({
            id: item.id,
            provider: item.provider?.id,
            url: item.url
        })),

        errors: {},
        ready: false
    });

    const validateLicenses = (items) => {
        const errors = []

        const providers = {};
        data.providers.forEach(item => providers[item.id] = item);

        if (!items?.length) {
            return errors;
        }

        if (items.some(item => !item.provider || !providers[item.provider])) {
            errors.push(t('pages.licenses.tracks.fields.providers.error'));
        }

        items.forEach(item => {
            if (providers[item.provider]?.regExp) {
                let regexStr = providers[item.provider].regExp;
                // ÑƒÐ±Ð¸Ñ€Ð°ÐµÐ¼ Ð¾Ð±Ñ‘Ñ€Ñ‚Ð¾Ñ‡Ð½Ñ‹Ðµ ÑÐ»ÑÑˆÐ¸ Ð² Ð½Ð°Ñ‡Ð°Ð»Ðµ/ÐºÐ¾Ð½Ñ†Ðµ
                const match = regexStr.match(/^\/(.+)\/([gimsuy]*)$/);
                const re = match ? new RegExp(match[1], match[2]) : new RegExp(regexStr);

                // Ð¿Ñ€Ð¾Ð²ÐµÑ€ÐºÐ°
                if (!item.url || !re.test(item.url)) {
                    errors.push(t('pages.licenses.tracks.fields.reg-exp.error', {
                        url: item.url,
                        format: providers[item.provider].placeholder
                    }));
                }
            }
        })

        return errors;
    }

    const onProviderChange = (payload, item) => {
        item.provider = payload?.value;

        update();
    }

    const onUrlChange = (e, item) => {
        item.url = e.target.value?.trim();

        update();
    }

    const remove = (item) => {
        if (data.licenses.indexOf(item) !== -1) {
            data.licenses.splice(data.licenses.indexOf(item), 1);

            update();
        }
    }

    const update = () => {
        emit('change', { licenses: data.licenses });

        data.errors.licenses = validateLicenses(data.licenses);

        emit('error', data.errors);
    }

    onMounted(async() => {
        data.providers = ((await api.licenses().providers.list())?.items || []).map(item => ({
            ...item,
            code: item.id
        }));
        data.ready = true;
    });
</script>

<style lang="scss" scoped>
    .form-line {
        &.view-mode-table {
            padding: 0 8px;
            &:first-child {
                padding-top: 16px;
            }
            &:last-child {
                padding-bottom: 16px;
            }
        }
    }
</style>
